.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

:root{
    --background: #F7F6F1;
    --primary-font: #204665;
    --secondary-font: #646967;
    --tertiary-font: #7A8FA0;
    --highlight: #FBF4B0;
    --fontBlue: #204665;
    --fontGrey: #646967;
    --backBlue: #E2EAF1;
    --backGrey: #ECECEC;

    --serif: 'Libre Baskerville', serif;
    --sans-primary: 'Quicksand', sans-serif;
    --sans-secondary: 'Lato', sans-serif;
}

body{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-color: #F7F6F1;
    background-color: var(--background);
}

.main-container{
    margin-left: 150px;
    margin-right: 100px;
  
}

.main-text{
    width:100%;
}

.landing-page{
    margin-bottom: 100px;

}


h1{
    font-family: 'Quicksand', sans-serif;
    font-family: var(--sans-primary);
    color: #204665;
    color: var(--primary-font);
    width: 50%;
    margin-top: 30px;
    padding-top: 100px;
    font-weight: 400;
    font-size: 32px;
    line-height: 200%;

}

.landing-page h3{
    font-family: 'Libre Baskerville', serif;
    font-family: var(--serif);
    color: #204665;
    color: var(--primary-font);
    width: 50%;
    font-size: 18px;
    font-weight: 350;
    line-height: 200%;
}
.underline{
    background-position: 0% -80%;
    background-image: linear-gradient(to top, #F7F6F1 50%, #E2EAF1 50%);
    background-image: linear-gradient(to top, var(--background) 50%, var(--backBlue) 50%);
    background-size: auto 200%;
    transition: background-position 0.3s ease-out 0s;
}

.underline:hover{
    background-position: 0% 0%;
}

.btn{
    padding: 3px 6px;
    border-radius: 3px;
    border: none;
    cursor: pointer;
}

.main-btns{
    margin-top: 30px;
}

.btn-blue{
    background-color: #E2EAF1;
    background-color: var(--backBlue);
    color: #204665;
    color: var(--fontBlue);
    margin-right: 20px;
    margin-bottom: 20px;
    font-family: 'Libre Baskerville', serif;
    font-family: var(--serif);
    font-size: 20px;
    padding: 10px 24px;
    border-radius: 15px;
    border: none;
    outline: none;
    box-shadow: 3px 3px 0 0 #204665;
    box-shadow: 3px 3px 0 0 var(--fontBlue);
}

.btn-grey{
    background-color: #ECECEC;
    background-color: var(--backGrey);
    color: #646967;
    color: var(--fontGrey);
    margin-right: 20px;
    margin-bottom: 20px;
    font-family: 'Libre Baskerville', serif;
    font-family: var(--serif);
    font-size: 20px;
    padding: 10px 24px;
    border-radius: 15px;
    border: none;
    outline: none;
    box-shadow: 3px 3px 0 0 #646967;
    box-shadow: 3px 3px 0 0 var(--fontGrey);

}

.small{
    font-size: 16px;
    padding: 5px 15px;
}

.btn-blue:hover{
    background-color: #ECECEC;
    background-color: var(--backGrey);
    color: #646967;
    color: var(--fontGrey);
    transition: all 0.3s ease-out;
    box-shadow: 3px 3px 0 0 #646967;
    box-shadow: 3px 3px 0 0 var(--fontGrey);
    cursor: pointer;
}

.projects-display{
    margin-bottom: 50px;
}

.cards{
    display: flex;
    flex-wrap: wrap;
    justify-content: left;
}

.project-card{
    margin-left: 0;
}



.project-grid{
    justify-content: center;
    margin-left: 0;
}

@media only screen and (max-width: 960px){
    .landing-page h1{
        width: 90%;
        margin-right: 10px;
        justify-content: center;
    }
    .landing-page h3{
        width: 90%;
        justify-content: center;
    }

    .main-container{
        margin-left: 50px;
        justify-content: center;
        margin-right: 0;

      
    }

